.b-devices {
  position: relative;
  background: white; }
  .b-devices__wrap {
    display: flex; }
    @media screen and (max-width: 680px) {
      .b-devices__wrap {
        display: block; } }
  .b-devices__block {
    flex: 0 0 50%;
    max-width: 50vw;
    min-width: 0;
    position: relative; }
    @media screen and (max-width: 680px) {
      .b-devices__block {
        max-width: 100%; }
        .b-devices__block:nth-child(2) {
          background: red; }
          .b-devices__block:nth-child(2) .b-devices__slider .item {
            min-height: 600px; }
            .b-devices__block:nth-child(2) .b-devices__slider .item__name {
              bottom: 160px; }
          .b-devices__block:nth-child(2) .b-devices__slider-arrows {
            bottom: 100px; } }
  .b-devices__slider {
    max-width: 100%;
    max-height: 100%; }
    .b-devices__slider-arrows {
      position: absolute;
      bottom: 132px;
      width: 100px;
      margin: auto;
      left: 0;
      right: 0;
      display: flex;
      justify-content: space-between; }
      @media screen and (max-width: 768px) {
        .b-devices__slider-arrows {
          bottom: 50px; } }
    .b-devices__slider .item {
      position: relative;
      height: 100vh;
      min-height: 500px;
      background: center/cover no-repeat; }
      .b-devices__slider .item__name {
        position: absolute;
        top: 50%;
        left: 100px;
        transform: translateY(-50%);
        max-height: 30px; }
        @media screen and (max-width: 768px) {
          .b-devices__slider .item__name {
            left: 50%;
            top: auto;
            transform: translate(-50%, 0);
            bottom: 120px; } }
  .b-devices .bottom-button-wrap {
    max-width: 180px; }

.arrow {
  cursor: pointer; }
